  @Watch('$route.name')
  changeTab(status: string): void {
    this.resetData()
    //@ts-ignore
    const filters = this.getFilter(this.$route.name)
    if (filters) {
      this.params = assign(this.params, filters.filters)
      this.checkRouteStatus(status)
    } else {
      this.params = {
        search: '',
        page: 1,
        limit: 20,
        orderByClause: '1',
        searchType: 'CONTRACT'
      }
      this.checkRouteStatus(status)
    }

    const resetScroll = document.getElementsByClassName('el-table__body-wrapper')[0]
    if (resetScroll) {
      resetScroll.scrollTop = 0
      resetScroll.scrollLeft = 0
    }
    // this.setParamsForUser()
    this.init()
  }

  checkBetweenDay(day: string | number): boolean {
    const time = 8.64e7
    const timeTomorrow = new Date(day).getTime() + time
    return new Date().getTime() >= new Date(day).getTime() && new Date().getTime() <= timeTomorrow
  }

  checkSaleMember(row: Record<string, any>): boolean {
    const isOfficeMember = this.role.userRole.length === 1 && this.role.userRole.find((role: string) => role === 'TRANSACTION_OFFICE_MEMBER')
    // if user là người tạo hộ người khác, và trong vòng 24h thì dc sửa
    if (isOfficeMember && row.createdBy === this.user.id && row.salesMemberId !== this.user.memberId && !this.checkBetweenDay(row.createdAt)) {
      return false
    }
    return true
  }

  showActionDestroy(row: Record<string, any>): boolean {
    let now = new Date()
    let check = false
    if (row.allowCancelDate) {
      let allowCancelDate = new Date(row.allowCancelDate)
      check = allowCancelDate > now
    } else {
      check = true
    }
    if (row.contractStatus !== '2' && check) {
      return true
    }
    return false
  }
