  handleActionAudit(params: { status: string; row: Record<string, any> }): void {
    const { status, row } = params
    if (status == 'destroy') {
      const listMessage = []
      for (const item in row) {
        if (item == 'applyFormNo' && row[item]) {
          listMessage.push(row[item])
        }
        if (item == 'contractNo' && row[item]) {
          listMessage.push(row[item])
        }
        if (item == 'totalAmount' && row[item]) {
          listMessage.push(this.$options.filters?.formatNumberDot(row[item]))
        }
      }
      const message = listMessage ? listMessage.join(' / ') : ''
      this.dataPopupconfirm = {
        title: 'Xác nhận hủy',
        text: 'Bạn có chắc chắn hủy',
        message: message,
        ids: [row.id]
      }
      this.setOpenPopup({
        popupName: 'popup-confirm',
        isOpen: true
      })
    }
    if (status == 'destroy-detail') {
      if (row.subContracts.length > 0) {
        this.dataPopupconfirm.ids = row.subContracts.map((item: Record<string, any>) => item.id)
        const message = row.subContracts
          .map((item: Record<string, any>) => `${row.applyFormNo ? row.applyFormNo + ' / ' : ''}${item.contractNo ? item.contractNo : ''}`)
          .join(', ')
        this.dataPopupconfirm.message = message
      } else {
        this.dataPopupconfirm.ids = [row.id]
        this.dataPopupconfirm.message = `${row.applyFormNo ? row.applyFormNo + ' / ' : ''}${row.contractNo ? row.contractNo : ''}`
      }
      this.setOpenPopup({
        popupName: 'popup-confirm',
        isOpen: true
      })
    }
    if (status == 'edit') {
      this.dataEdit.code = status
      this.dataEdit.bankContractNo = row.bankContractNo
      this.dataEdit.type = 'audit'
      this.setOpenPopup({
        popupName: 'create-cost',
        isOpen: true
      })
    }
    if (status == 'delete') {
      const listMessage = []
      for (const item in row) {
        if (item == 'applyFormNo' && row[item]) {
          listMessage.push(row[item])
        }
        if (item == 'contractNo' && row[item]) {
          listMessage.push(row[item])
        }
        if (item == 'productCode' && row[item]) {
          listMessage.push(row[item])
        }
        if (item == 'totalAmount' && row[item]) {
          listMessage.push(this.$options.filters?.formatNumberDot(row[item]))
        }
      }
      const message = listMessage ? listMessage.join(' / ') : ''
      this.dataPopupconfirm = {
        title: 'Xác nhận xóa',
        text: 'Bạn có chắc chắn xóa',
        message: message,
        ids: [row.id]
      }
      this.setOpenPopup({
        popupName: 'popupDeleteContract',
        isOpen: true
      })
    }
  }
